<template>
  <div class="admin-login">
      <div class="admin-login-box">
          <h1>管理员登录</h1>
          <el-form  :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form" >
             <el-form-item  prop="username">
                <el-input
                  placeholder="输入用户名"
                  size="small"
                  v-model="ruleForm.username">
                </el-input>
             </el-form-item>
              <el-form-item  prop="password">
                <el-input
                  placeholder="输入密码"
                  size="small"
                  type="password"
                  v-model="ruleForm.password">
                </el-input>
             </el-form-item>
             <el-form-item>
                 <el-button  size="small" :loading="loading"  @click="submitForm('ruleForm')" style="width:100%;" type="primary">{{loading ?'正在登陆中' :'立即登录'}}</el-button>
              </el-form-item>
          </el-form>

      </div>
  </div>
</template>

<script>
import * as api from '../api.js';

export default {
  name: 'index',
  data:function(){
    return {
      ruleForm: {
        username:'',
        password:'',
      },
      loading:false,
      rules: {
          username: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'change' }
          ],

        }
    };
  },
  created(){
     if(this.$store.getters.appToken){
         this.$router.replace('/dashboard/index');
     }
  },
  methods:{
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
               this.loading = true;
              api.post('shop/admin.passport/login',{
                  managename:this.ruleForm.username,
                  password:this.ruleForm.password,
              }).then(res=>{
                  this.loading = false;
                  this.$message({
                      message: '登录成功',
                      type: 'success'
                  });
                  var that  = this;
                  this.$store.dispatch('setAppInfo',res).then(()=>{
                    
                      setTimeout(function () {
                          that.$router.replace("/dashboard/index");
                      }, 1000);
                  });
              }).catch((error) => {
                  this.loading =false;
              });
          } else {
            return false;
          }
        });
      },

  }
}
</script>
<style scoped>
  .goback{
    margin-left: 10px;
    color: #909399;
    font-size: 14px;
  }
  .admin-login{
    position:fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #0078E0;
  }
  .el-button--primary{
      background: #0078E0;
  }
   .el-button--primary:active{
      background: #3199f58a;
  }
  .admin-login-box{
    width: 300px;
    height:260px;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 20px 30px 0 rgba(0,0,0, 0.1);
    position: absolute;
    transform:translate(-50%,-50%);
    top:50%;
    left:50%;
  }

  .admin-login-box h1{
        font-size: 22px;
        color: #373d40;
        line-height: 1;
        padding: 5px 0;
        text-align: center;
        padding: 20px 0;
  }
  .login-form{
    padding: 0 40px;
  }
</style>
